@charset "UTF-8";
:root {
  --color-green: #00aa4b;
  --color-white: #fff;
  --color-dark: #171717;
  --color-black: #232323;
  --color-text: #121212;
  --color-gray:  #e8e7e7;
  --color-tundora: #f1f1f1;
  --color-russet: #ffaf2d;
  --color-lightgray: #777777;
  --color-golden:#ff8c00;
  --color-fortitle: #343434;
  --color-forlink:#303030;
}

@font-face {
  font-family: "MyriadPro";
  src: url("../fonts/MyriadPro-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "MyriadPro";
  src: url("../fonts/MyriadPro-SemiExtended-Light.woff2") format("woff2");
  font-weight: 300;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "MyriadPro";
  src: url("../fonts/MyriadPro-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "MyriadPro";
  src: url("../fonts/MyriadPro-SemiExtended-Light.woff2") format("woff2");
  font-weight: 300;
  font-display: swap;
  font-style: normal;
}

*, *::before, *::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "MyriadPro", sans-serif;
  font-weight: 400;
  background-color: var(--color-gray);
  overflow-x: hidden;
}

body.active-body::after {
  content: '';
  position: absolute;
  z-index: 9;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background-color: #000;
  opacity: 0.5;
}

.container {
  max-width: 1550px;
  margin: 0 auto;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

.header {
  margin-bottom: 21px;
}

.header__fix {
  width: 100%;
  background-color: var(--color-green);
}

.header .fix__list {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header .fix__item {
  padding: 14px 0;
}

.header .fix__item:hover .fix__link {
  color: white;
  transition: color 0.3s linear;
}

.header .fix__item .fix__link--email {
  position: relative;
}

.header .fix__item .message {
  position: absolute;
  top: 32px;
  left: -9px;
  z-index: 2;
  display: none;
  justify-content: center;
  align-items: center;
  width: 163px;
  height: 45px;
  box-shadow: 4px 6px 24px rgba(0, 0, 0, 0.49);
  border-radius: 23px;
  background-color: var(--color-black);
  color: var(--color-white);
  font-size: 16px;
}

.header .fix__item .message.active {
  display: flex;
}

.header .fix__item:nth-child(3) .fix__link {
  margin-right: 3px;
  position: relative;
}

.header .fix__item:nth-child(3) .fix__link::after {
  content: '';
  position: absolute;
  right: -23px;
  bottom: -5px;
  width: 1px;
  height: 28px;
  border: 1px solid var(--color-white);
  background-color: #d5bee7;
  opacity: 0.25;
}

.header .fix__item:nth-child(7) .fix__link {
  position: relative;
}

.header .fix__item:nth-child(7) .fix__link::after {
  content: '';
  position: absolute;
  right: -23px;
  bottom: -5px;
  width: 1px;
  height: 28px;
  border: 1px solid var(--color-white);
  background-color: #d5bee7;
  opacity: 0.25;
}

.header .fix__item:nth-child(10) .fix__link {
  margin-right: -14px;
  display: flex;
  align-items: center;
}

.header .fix__item:nth-child(10) .fix__link:before {
  content: '';
  display: inline-block;
  width: 20px;
  height: 15px;
  margin-right: 7px;
  background: url(../../img/email.png) no-repeat center/cover;
}

.header .fix__item:last-child .fix__link {
  margin-right: 1px;
  display: flex;
  align-items: center;
}

.header .fix__item:last-child .fix__link:before {
  content: '';
  display: inline-block;
  width: 16px;
  height: 20px;
  margin-right: 5px;
  background: url(../../img/telephone.png) no-repeat center/cover;
}

.header .fix__link {
  color: #e9f5ec;
  font-size: 16px;
  text-align: center;
  transition: color 0.3s linear;
}

.search {
  background-color: var(--color-gray);
}

.search input[type=text]::-ms-clear {
  display: none;
  width: 0;
  height: 0;
}

.search input[type=text]::-ms-reveal {
  display: none;
  width: 0;
  height: 0;
}

.search input[type="search"]::-webkit-search-decoration,
.search input[type="search"]::-webkit-search-cancel-button,
.search input[type="search"]::-webkit-search-results-button,
.search input[type="search"]::-webkit-search-results-decoration {
  display: none;
}

.search__container {
  display: flex;
  align-items: center;
}

.search__logo {
  display: flex;
  align-items: center;
  margin-right: 89px;
}

.search__logo .logo__img {
  display: block;
  width: 40px;
  height: 40px;
  object-fit: cover;
  margin-right: 8px;
}

.search__logo .logo__title {
  display: block;
  color: var(--color-dark);
  font-size: 33px;
}

.search__wrap {
  display: flex;
  align-items: center;
  width: 100%;
  max-width: 885px;
  height: 35px;
  margin: 15px 49px 15px 0;
  border: 1px solid transparent;
  border-radius: 5px;
}

.search__select {
  position: relative;
  padding-left: 17px;
  padding-right: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  border: 1px solid var(--color-gray);
  background-color: var(--color-tundora);
  cursor: pointer;
}

.search__select::after {
  content: '';
  display: inline-block;
  width: 12px;
  height: 6px;
  background: url(../../img/mark.png) no-repeat center/cover;
  margin-left: 6px;
}

.search__select .select__option {
  display: none;
  position: absolute;
  top: 30px;
  left: 0;
  z-index: 10;
  width: 215px;
  height: 368px;
  border: 1px solid #e8e7e7;
  background-color: #f1f1f1;
}

.search__select .select__option ul {
  padding-left: 15px;
  padding-top: 20px;
}

.search__select .select__option ul li {
  font-size: 16px;
  color: #777777;
  font-weight: 300;
}

.search__select .select__option ul li:first-child {
  margin-bottom: 20px;
}

.search__select .select__option ul li a {
  display: block;
  padding-bottom: 5px;
  color: #343434;
  font-weight: 400;
  transition: color 0.3s linear;
}

.search__select .select__option ul li a:hover {
  color: #00aa4b;
  transition: color 0.3s linear;
}

.search__select .select__option.active {
  display: block;
}

.search__label {
  display: flex;
  align-items: center;
  flex-grow: 2;
}

.search__input {
  height: 35px;
  border: none;
  flex-grow: 2;
  padding-left: 15px;
  outline: none;
}

.search__btn {
  width: 47px;
  height: 35px;
  border: none;
  background-color: var(--color-russet);
  background-image: url(../../img/лпа.png);
  background-repeat: no-repeat;
  background-size: 21px 22px;
  background-position: center;
  outline: none;
  opacity: 1;
  transition: opacity 0.3s linear;
}

.search__btn:hover {
  opacity: 0.7;
  transition: opacity 0.3s linear;
}

.search__text {
  display: inline-block;
  max-width: 95px;
  color: var(--color-lightgray);
  font-family: "MyriadPro";
  font-size: 14px;
  font-weight: 300;
  line-height: 16px;
  margin-right: 38px;
}

.search__img {
  width: 35px;
  height: 35px;
  opacity: 0.9;
  margin-right: 5px;
}

.basket {
  position: relative;
  width: 125px;
  height: 65px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: transparent;
  transition: background-color 0.3s linear;
  cursor: pointer;
}

.basket:hover {
  background-color: var(--color-russet);
}

.basket:hover .basket__price {
  color: var(--color-white);
  transition: color 0.3s linear;
}

.basket:hover .basket__text {
  color: var(--color-white);
  transition: color 0.3s linear;
}

.basket:hover .basket__number {
  color: var(--color-white);
  transition: color 0.3s linear;
}

.basket__img {
  display: block;
  width: 29px;
  height: 27px;
  margin-right: 12px;
  object-fit: cover;
}

.basket__number {
  position: absolute;
  top: 7px;
  left: 10px;
  color: var(--color-golden);
  font-size: 14px;
  line-height: 16px;
  transition: color 0.3s linear;
}

.basket__text {
  transition: color 0.3s linear;
  color: var(--color-lightgray);
  font-family: "MyriadPro";
  font-size: 14px;
  font-weight: 300;
  line-height: 16px;
}

.basket__price {
  transition: color 0.3s linear;
  color: var(--color-dark);
  font-size: 16px;
  line-height: 18px;
}

.nav {
  height: 100px;
  box-shadow: 4px 6px 18px rgba(0, 0, 0, 0.11);
  background-color: var(--color-white);
}

.nav__container {
  height: 100%;
}

.nav__list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}

.nav__item {
  margin-left: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-basis: 9.0909%;
  cursor: pointer;
}

.nav__item:first-child {
  margin-left: 0;
  align-items: flex-start;
}

.nav__item:nth-child(2) {
  margin-left: -24px;
}

.nav__item:nth-child(3) {
  margin-left: 15px;
}

.nav__item:nth-child(4) {
  margin-left: 20px;
}

.nav__item:nth-child(5) {
  margin-left: -2px;
}

.nav__item:nth-child(6) {
  margin-left: 9px;
}

.nav__item:nth-child(7) {
  margin-left: 9px;
}

.nav__item:nth-child(8) {
  margin-left: 13px;
}

.nav__item:nth-child(9) {
  margin-left: 10px;
}

.nav__item:nth-child(10) {
  padding-left: 13px;
}

.nav__item:last-child {
  padding-left: 31px;
}

.nav__box {
  position: relative;
  width: 57px;
  height: 57px;
  background-image: linear-gradient(to top, #dbdbdb 0%, #f3f3f3 100%);
  border-radius: 50%;
  margin-bottom: 4px;
}

.nav__img {
  position: absolute;
  top: 0;
  right: 0;
  display: block;
  object-fit: cover;
}

.nav__des {
  color: var(--color-fortitle);
  font-size: 16px;
  font-weight: 400;
}

.modal {
  position: absolute;
  z-index: 5;
  top: 49px;
  right: -1000px;
  z-index: 10;
  display: flex;
  justify-content: space-between;
  width: 754px;
  height: 364px;
  background-color: var(--color-white);
  transition: right 0.5s linear;
}

.modal__tel {
  padding: 60px 0 60px 51px;
}

.modal__numder {
  color: #00aa4b;
  font-size: 24px;
}

.modal__des {
  display: block;
  color: var(--color-lightgray);
  font-family: "MyriadPro";
  font-size: 16px;
  font-weight: 300;
  margin-bottom: 23px;
}

.modal__list {
  font-family: "MyriadPro";
  font-size: 16px;
  font-weight: 300;
}

.modal__list li:first-child {
  margin-top: 20px;
}

.modal__list li {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 10px;
}

.modal__list li a {
  margin-left: 11px;
  font-family: "MyriadPro";
  font-weight: 300;
  color: var(--color-lightgray);
  overflow: hidden;
}

.modal__list li a::after {
  content: '';
  display: block;
  height: 1px;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  transform: translateX(-300px);
  transition: transform 0.5s linear;
}

.modal__list li a:hover::after {
  transform: translateX(0);
  transition: transform 0.5s linear;
}

.modal__list li img {
  display: block;
  width: 26px;
  height: 27px;
  object-fit: cover;
}

.modal__list li img:first-child {
  object-fit: contain;
}

.modal__close {
  position: absolute;
  top: 17px;
  right: 17px;
  width: 17px;
  height: 21px;
  color: #777777;
  font-family: "MyriadPro";
  font-size: 30px;
  font-weight: 300;
  cursor: pointer;
}

.form {
  padding-top: 60px;
  padding-right: 63px;
}

.form__title {
  display: block;
  font-size: 24px;
  line-height: 20px;
  margin-bottom: 10px;
}

.form__subtitle {
  display: block;
  color: var(--color-lightgray);
  font-family: "MyriadPro";
  font-weight: 300;
  line-height: 20px;
  margin-bottom: 28px;
}

.form__name figcaption {
  display: block;
  color: var(--color-lightgray);
  font-family: "MyriadPro";
  font-weight: 300;
  line-height: 20px;
}

.form__name input {
  display: block;
  border: none;
  outline: none;
  height: 25px;
  padding-left: 13px;
  border-bottom: 1px solid var(--color-dark);
  transition: border-bottom 0.3s linear;
  margin-bottom: 20px;
}

.form__name input:focus {
  border-bottom: 1px solid var(--color-golden);
  transition: border-bottom 0.3s linear;
}

.form__name input::placeholder {
  font-size: 18px;
}

.form__btn {
  display: block;
  width: 133px;
  height: 45px;
  border: none;
  outline: none;
  border-radius: 23px;
  background-color: var(--color-russet);
  color: var(--color-white);
  font-size: 18px;
  cursor: pointer;
  transition: opacity 0.3s linear;
}

.form__btn:hover {
  opacity: 0.7;
  transition: opacity 0.3s linear;
}

.modal.active {
  right: 0px;
  transition: right 0.5s linear;
}

.header-mob__top {
  height: 100px;
