}

.header-mob__logo {
  max-width: 255px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-right: 107px;
}

.header-mob__logo img {
  display: block;
  width: 55px;
  height: 55px;
  object-fit: cover;
  cursor: pointer;
}

.header-mob__title {
  padding-left: 10px;
}

.header-mob__title strong {
  font-size: 30px;
  color: #353535;
}

.header-mob__title span {
  font-size: 18px;
  color: #777777;
  font-weight: 300;
}

.header-mob__search {
  display: block;
  width: 51px;
  height: 57px;
  object-fit: cover;
  cursor: pointer;
  margin-right: 41px;
}

.header-mob__tel {
  display: block;
  width: 45px;
  height: 56px;
  object-fit: cover;
  cursor: pointer;
  margin-right: 33px;
}

.header-mob__backet {
  position: relative;
}

.header-mob__backet img {
  display: block;
  width: 56px;
  height: 50px;
  object-fit: cover;
  cursor: pointer;
  margin-right: 33px;
}

.header-mob__backet span {
  position: absolute;
  top: -25px;
  left: 20px;
  font-size: 25px;
  color: #fda11c;
}

.greeting {
  max-height: 476px;
  margin-bottom: 20px;
}

.greeting__content {
  display: flex;
  justify-content: space-between;
}

.greeting__slider {
  position: relative;
  width: 58%;
  height: 476px;
  margin-right: 21px;
}

.menu {
  width: 37%;
  max-width: 303px;
  background-color: var(--color-white);
  padding-top: 18px;
  margin-right: 21px;
}

.menu__title {
  color: var(--color-russet);
  padding-left: 27px;
  font-size: 24px;
  line-height: 45px;
}

.menu__list {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-top: 6px;
}

.menu__item {
  display: flex;
  align-items: center;
  height: 45px;
  border-bottom: 1px solid rgba(186, 186, 186, 0.25);
  padding-left: 27px;
}

.menu__item:last-child {
  border-bottom: none;
}

.menu__item:hover .menu__link {
  color: var(--color-russet);
  transition: color 0.3s linear;
}

.menu__item:hover .menu__link::after {
  color: var(--color-russet);
  transition: color 0.3s linear;
}

.menu__link {
  position: relative;
  display: block;
  width: 100%;
  color: var(--color-forlink);
  font-family: "MyriadPro";
  font-size: 16px;
  font-weight: 300;
  line-height: 45px;
  transition: color 0.3s linear;
}

.menu__link::after {
  content: '>';
  position: absolute;
  top: 50%;
  right: 21px;
  transform: translateY(-50%);
  color: var(--color-forlink);
  transition: color 0.3s linear;
}

