.swiper-container-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet {
  margin: 0px 6px;
}

.advertisement {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding-left: 30px;
  width: 28%;
  border-radius: 2px;
  border: 2px solid var(--color-russet);
