}

.brand {
  margin-bottom: 164px;
}

.brand__list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 101px;
  background-color: var(--color-white);
}

.brand__item {
  position: relative;
  flex-basis: 16.666%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.brand__item::after {
  content: '';
  position: absolute;
  bottom: -17px;
  right: 0;
  display: block;
  width: 2px;
  height: 71px;
  opacity: 0.5;
